/**
 * Function to generate data-testid
 * Input: <cy-button data-testid="alert-create">Default</cy-button>
 * Output: <cy-button data-testid="alert-create-button">Default</cy-button>
 */
export function generateTestId(attrs, compType) {
  let attr = {}
  const cyTestId = attrs['data-testid']
  if (cyTestId) attr['data-testid'] = `${cyTestId}-${compType}`
  return attr
}

/**
 * Function to exclude all attributes except "id", "class" and "data-*" (data-testid will be excuded)
 */
export function bindCustomAttributes(attrs) {
  let allowedAttrs = {}
  const regex = /^data-(?!testid$).*/

  Object.keys(attrs).forEach(key => {
    if(key === 'id' || key === 'class' || regex.test(key)) {
      allowedAttrs[key] = attrs[key]
    }
  })
  return allowedAttrs
}